"use client"
import CartItem from "@/components/CartItem";
import Container from "@/components/Container";
import { useShopingCartContext } from "@/context/ShopingCartContext";
import React from "react";


export default function Cart() {

    const { cartItems } = useShopingCartContext();

    return (
        <Container>
            <h1 className="text-right my-3 mx-12 p-2 font-sans text-2xl">Shoping Cart</h1>


            <div className="">
                {
                    cartItems.map((item)=>(
                        <CartItem key={item.id} {...item}/>
                    ))
                }
            </div>

            <div className="border shadow-md p-4">
                <h3>
                    Total Price : <span>54$</span> </h3>
                <h3>
                    Is sale : <span>45$</span></h3>
                <h3>
                    End Price : <span>54$</span></h3>

                <div>
                    <input placeholder="Fix Your Code To Sale" type="text" />
                    <button className="bg-sky-600 text-white px-4 py-1 rounded"> Click in</button>
                </div>
            </div>
            <hr />

        </Container>
    )
}